<template>
  <div>
    <el-card class="box-card">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="">
          <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="form.description"></el-input>
        </el-form-item>
        <el-form-item label="">
          <mavon-editor code_style="monokai-sublime" :ishljs = "true" v-model="form.content" ref="mavon" @imgAdd="$imgAdd" @imgDel="$imgDel"/>
        </el-form-item>
        <el-form-item label="">
          <el-upload
            class="avatar-uploader"
            :action="$axios.defaults.baseURL+'/upload'"
            :show-file-list="false"
            :on-success="res => $set(form, 'imgUrl', res.url)"
          >
            <img v-if="form.imgUrl!=''" :src="form.imgUrl" class="avatar" alt=""/>
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="">
          <el-tag
            :key="tag"
            v-for="tag in form.tags"
            closable
            :disable-transitions="false"
            @close="handleClose(tag)"
          >{{tag}}</el-tag>
          <el-input
            class="input-new-tag"
            v-if="inputVisible"
            v-model="inputValue"
            ref="saveTagInput"
            size="small"
            @keyup.enter.native="handleInputConfirm"
            @blur="handleInputConfirm"
          ></el-input>
          <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.author"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            type="datetime"
            placeholder=""
            v-model="form.time"
            style="width: 100%"
          ></el-date-picker>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="onSubmit">
            <span v-if="id == 'new'"></span>
            <span v-else></span>
          </el-button>
          <el-button @click="onCancel"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { mavonEditor } from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import 'mavon-editor/dist/markdown/github-markdown.min.css'
// import marked from "marked";
// import buildToc from './build-toc.js'
export default {
  components: {
    mavonEditor
  },
  data() {
    return {
      id: this.$route.params.id,
      // create edit
      mode: "create",
      dateFormat: "yyyy-MM-dd hh:mm:ss",
      inputVisible: false,
      inputValue: "",
      form: {
        title: "",
        description: "",
        content: "",
        imgUrl: "",
        tags: [],
        author: "",
        time: new Date()
      }
    };
  },
  created() {
    this.id != 'new' && this.getBlogsList()
  },
  methods: {
    async getBlogsList() {
      let res = await this.$axios.get(`/blogs/${this.id}`)
      console.log(res)
      this.form = res.data
      // this.form.imgUrl = 'http://coco727.oss-cn-hongkong.aliyuncs.com/2e667ea22844cbcafbdcc374dc27c9ce.jpg'
    },
    handleClose(tag) {
      this.form.tags.splice(this.form.tags.indexOf(tag), 1);
    },

    showInput() {
      this.inputVisible = true;
      this.$nextTick(() => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },

    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.form.tags.push(inputValue);
      }
      this.inputVisible = false;
      this.inputValue = "";
    },
    onSubmit() {
      console.log(this.form.content)
      // console.log(marked(this.form.content))
      let data = {
        title: this.form.title,
        description: this.form.description,
        content: this.form.content,
        imgUrl: this.form.imgUrl,
        tags: this.form.tags,
        author: this.form.author,
        time: this.form.time,
        id: this.id
      };
      if (!data.title || !data.content) {
        this.$alert("");
      }
      let res;
      if (this.id == "new") {
        res = this.$axios.post("/blogs", data);
      } else {
        res = this.$axios.put(`/blogs/${this.id}`, data);
      }
      console.log(res);
      //   let url;
      //   let method;
      //   if (this.mode === "edit") {
      //     url = "/api/blog/" + this.id;
      //     method = "PUT";
      //   } else {
      //     url = "/api/blog/0";
      //     method = "POST";
      //   }
      //   return fetch(url, {
      //     method,
      //     credentials: "same-origin",
      //     headers: {
      //       "Content-Type": "application/json"
      //     },
      //     body: JSON.stringify(data)
      //   })
      //     .then(response => response.json())
      //     .then(() => {
      //       let msg = this.mode === "edit" ? "" : "";
      //       this.$alert(msg, {
      //         callback: action => {
      //           this.$router.push("/content-article");
      //         }
      //       });
      //     });
    },
    onCancel() {
      this.$router.push({
        path: "/blogs/list"
      });
    },
    handleAvatarSuccess(res) {
      console.log(res);
      this.form.imgUrl = res.url;
      console.log(this.form);     
    },
    // @imgAdd event
    $imgAdd(pos, $file){
        // ..
        var formData = new FormData();
        formData.append('file', $file);
        this.$axios.post('/upload',formData)
        .then((res) => {
          console.log(res)
            // .url![...](0) -> ![...](url)
            /**
           * $vm mavonEditor
           * 1. : `import {mavonEditor} from ...` `$vm``mavonEditor`
           * 2. $refs: htmlref : `<mavon-editor ref=md ></mavon-editor>`$vm` `this.$refs.md`
           */
            this.$refs.mavon.$img2Url(pos, res.data.url);
        })
    },
    $imgDel(pos, $file) {
      console.log(pos, $file)
    }
  }
};
</script>


